@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base{

body{
    @apply bg-slate-900 font-body text-white ;
}
}

@layer components{
    .page-container{
        max-width: 1280px;
       @apply mx-auto;
    }
    .movie-list .swiper-slide {
        width: 290px;
        height: auto;
    }
}


